* {
  --activ-color: #00acdc;
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.img__link {
  display: inline-block;
}

.logo {
  display: block;
}

.nav__list {
  text-transform: uppercase;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  height: 60px;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #000;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: var(--activ-color);
}

.is-active {
  color: var(--activ-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--activ-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
